.my-skills-main-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin: 0 auto;
    opacity: 1;
    padding: 0;
}

.my-skills-wrapper {
    position: absolute;
    width: calc(100% - 50px);
    height: 100%;
    padding: 0 25px;
    top: -50vh;
    left: 0;
    opacity: 0;
    will-change: transform, opacity;
}

#my-skills-title {
    font-size: 32px;
    font-weight: bolder;
}

.my-skills-info-container {
    position: relative;
    top: 50%;
    max-width: 700px;
    max-height: 519px;
    z-index: 99;
    background: linear-gradient(to bottom right, #090909 0%, #000 100%);
    border-radius: 20px;
    margin: 0 auto;
    opacity: .93;
    padding: 20px 60px;
    transform: translateY(-50%);
    min-width: 190px;
    overflow: hidden;
}

table {
    font-size: 14px;
    text-align: center;
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    max-width: 700px;
    margin: 15px auto 0;
    }
    th, td:first-child {
    background: transparent;
    color: white;
    padding: 10px;
    }
    th, td {
        border: none;
        background: transparent;
        margin: 0;
        width: 20%;
    }

    th:first-child, td:first-child {
        text-align: left;
    }

    .skill-rating-icon {
        width: 16px;
        height: 16px;
    }

    @media screen and (max-width: 910px) {
        .my-skills-info-container {
            padding: 15px 45px;
        }
        
        .my-skills-info-container {
            opacity: .88;
        }
    }

    @media screen and (max-width: 800px) {
        table {
            font-size: 12px;
        }

        .skill-rating-icon {
            width: 14px;
            height: 14px;
        }

        #my-skills-title {
            font-size: 28px;
        }

        .my-skills-info-container {
            border-radius: 20px;
            padding: 10px 30px;
        }
    }

