@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');
/* #2a487c #6991db */
html, body {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #6b6463;
  touch-action: pan-x pan-y;
}

p, span, div, h1, h2, h3 {
  -webkit-text-size-adjust:100%;
}

.show-el {
  animation: show-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-left-translation-anim {
  animation: left-translation-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-right-translation-anim {
  animation: right-translation-an 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-left-translation-anim-mob {
  animation: left-translation-an-mob 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

.show-right-translation-anim-mob {
  animation: right-translation-an-mob 500ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both 1;
}

@keyframes show-an {
  0% { opacity: 0; transform: translateY(100%); }
  100% { opacity: 1; transform: translateY(0%); top: 0; }
}

@keyframes left-translation-an {
  0% { opacity: 0; transform: translate(-12%, 100%); }
  100% { opacity: 1; transform: translate(-12%, 0%); top: 0; }
}

@keyframes right-translation-an {
  0% { opacity: 0; transform: translate(12%, 100%); }
  100% { opacity: 1; transform: translate(12%, 0%); top: 0; }
}

@keyframes left-translation-an-mob {
  0% { opacity: 0; transform: translate(0, 100%); }
  100% { opacity: 1; transform: translate(0, 0); top: 0; }
}

@keyframes right-translation-an-mob {
  0% { opacity: 0; transform: translate(0, 100%); }
  100% { opacity: 1; transform: translate(0, 0); top: 0; }
}

.background {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url(../src/images/Missing_dev.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-background-size: cover;
  filter: brightness(.5) blur(2px);
  -webkit-filter: brightness(.5) blur(2px);
  z-index: 1;
}

.main {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 999999;
  overflow-x: hidden;
  scroll-behavior: smooth;
  touch-action: pan-x pan-y;
  overflow-y: hidden;
}

canvas {
  touch-action: initial !important;
}

body {
  overflow-y: hidden;
}

button {
  cursor: pointer;
}

.App {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #6b6463;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  padding: 0;
  margin: 0;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #e4ba94 #6b6463;
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  width: 11px;
  width: 11px;
}
*::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: #6b6463;
}

*::-webkit-scrollbar-track:hover {
  background-color: #55504f;
}

*::-webkit-scrollbar-track:active {
  background-color: #e4ba94;
}

*::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #e4ba94;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #b79985;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #6b6463;
}

@media screen and (max-width: 768px) {
  .background {
    filter: brightness(.5) blur(1.5px);
    -webkit-filter: brightness(.5) blur(1.5px);
  }
}