.bottom-scene-container {
    position: relative;
    display: inline-block;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    padding: 25px 0 0 0;
    will-change: transform, opacity;
}

#bottom-title {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-size: 42px;
    text-align: center;
    padding: 0;
    left: 0;
    z-index: 99;
    top: 0;
    margin: 0 20px 0 0;
}

#c2 {
    vertical-align: middle;
    margin: 0;
    display: inline-block;
    position: relative;
}

@media screen and (max-width: 1280px) {
    #bottom-title {
        position: absolute;
        top: 50%;
        display: block;
        left: 50%;
        font-size: 36px;
        transform: translate(-50%, -50%);
        margin: 0;
    }
}

@media screen and (max-height: 750px) {
    #bottom-title {
        font-size: 28px;
    }
}

@media screen and (max-height: 600px) {
    #bottom-title {
        font-size: 24px;
    }
}

@media screen and (max-height: 450px) {
    #bottom-title {
        font-size: 20px;
    }
}

@media screen and (max-width: 768px) {
    #bottom-title {
        font-size: 32px;
    }
}

@media screen and (max-width: 445px) {
    #bottom-title {
        font-size: 28px;
    }
}

@media screen and (max-width: 300px) {
    #bottom-title {
        font-size: 24px;
    }
}

@media screen and (max-height: 600px) {
    #bottom-title {
        font-size: 28px;
    }
}

@media screen and (max-height: 500px) {
    #bottom-title {
        font-size: 24px;
    }
}

@media screen and (max-height: 400px) {
    #bottom-title {
        font-size: 20px;
    }
}